import type { Metadata } from 'next';
import './globals.css';
import { UserProvider, useUser } from '@/contexts/UserContext';
import DisplayNameDialog from '@/components/DisplayNameDialog';
import QuestionModal from '@/components/QuestionModal';

export const metadata: Metadata = {
  title: 'VoteBox - Make Your Voice Heard',
  description: 'Create polls, vote, and see results in real-time',
};

'use client';

// Modal Manager Component
function ModalManager() {
  const { isVerified, setIsVerified, isLoading } = useUser();

  // Don't show modals while loading
  if (isLoading) {
    return null;
  }

  return (
    <>
      {!isVerified && <QuestionModal onVerified={() => setIsVerified(true)} />}
      <DisplayNameDialog />
    </>
  );
}

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="en">
      <body>
        <UserProvider>
          <ModalManager />
          {children}
        </UserProvider>
      </body>
    </html>
  );
}